.search_holder{
    width: 85vw;
    height: 20vh;
    background-color: #1B3180;
    display: flex;
    flex-direction: row;
    justify-content: start;
    padding: 20px;
    box-sizing: border-box;
}

.search_bar{
    height: 12vh;
    width: 65vw;
    background-color: #1C2D7E;
    color: white;
    font-size: 20px;
    border: 1px solid black;
    padding: 10px;
    box-sizing: border-box;
    
}

.profile_holder {
    height: 12vh;
    color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}

.profile_img {
    border-radius: 50%;
    width: 80px;
    height: 80px;
}