.menu_holder{
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
    padding: 0 10px;
    box-sizing: border-box;
}


.menu_icon{
    margin-top: 10px;
    margin-right: 10px;
    width: 20px;
    height: 20px;
}

.menu_noti {
    width: 20px;
    height: 20px;
    background-color: green;
    border-radius: 50%;
}

.menu_holder p {
    margin: 10px 30px 0 0 ;
}