.profile_holder1{
    width: 150px;
    height: 250px;
    overflow: hidden;
    border-radius: 10px;
    border: 1px solid black;
    margin: 10px 40px;
    box-shadow: 2px 2px 2px gray;
    justify-content: space-evenly;
}

.movie_img{
    height: 160px;
    width: 150px;
    border-radius: 10px;
}

.profile_holder1 h4, h5{
    margin: 5px;
}

